@import "../utils/mixins";

.logo {
  &--menu {
    width: 130px;
    @include onDesktop() {
      width: 187px;
    }
  }
  &__image {
    width: 130px;
    color: #1d1d1d;

    @include onDesktop {
      height: 56px;
      width: 187px;
    }

    @include onTablet {
      height: 56px;
      width: 187px;
    }
  }
}
